$font-stack: 'Gilroy', "Helvetica Neue", sans-serif;
$font-second: 'Druk Wide Cyr', "Helvetica Neue", sans-serif;

$color-main: #c09f6f;
$color-white: #fff;
$color-hover: #d6ad72;

@import '../../node_modules/magnific-popup/src/css/main';
@import '../../node_modules/owl.carousel/dist/assets/owl.carousel.css';
@import '../../node_modules/slick-carousel/slick/slick.css';
@import '../../node_modules/ion-rangeslider/css/ion.rangeSlider.min.css';
@import '../../node_modules/select2/dist/css/select2.min.css';

@import 'main.css';


html {
	scroll-behavior: smooth;
}

body {
	font-family: $font-stack;
	margin: 0;
	padding: 0;
	background-color: #fff;
	overflow-x: hidden;
	position: relative;
	box-sizing: border-box;
	font-size: 16px;
	font-weight: 500;
	color: $color-white;

	&.ohidden {
		overflow: hidden;
	}
}

* {
	box-sizing: border-box;
}

a {
	text-decoration: none;
	transition: color 0.15s linear, background-color 0.15s linear;
}

img {
	display: block;

	&.lazyload {
		opacity: 0;
		transition: opacity 0.2s linear;

		&.load {
			opacity: 1;
		}
	}
}

/* width */
::-webkit-scrollbar {
	width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #F7F7F7;
	border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: $color-main;
	border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: $color-hover;
	border-radius: 5px;
}

.calendar-block {
	color: #000000;
	margin: 60px 0 0;

	colgroup {
		col {
			width: 120px !important;
		}
	}
}

.fc-timegrid-slots td {
	height: 135px !important;
}

.fc-timegrid-slot-label {
	width: 120px;
	background-color: #f6f6f6;
	font-family: $font-second;
	font-weight: bold;
	font-size: 14px;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	padding-top: 20px !important;
}

.fc-toolbar-title,
.fc-scrollgrid-sync-table,
.fc-timegrid-divider.fc-cell-shaded {
	display: none;
}

.select2-container {
	width: 100% !important;

	.select2-selection--multiple,
	.select2-container--default .select2-selection--multiple .select2-selection__rendered {
		height: 56px;
		border: 1px solid #dcdcdc;
		padding: 0 25px;
		border-radius: 0;
	}
}

.select2-container--default .select2-selection--multiple .select2-selection__rendered {
	height: 56px;
	display: flex;
	align-items: center;
	left: 27px;
	position: absolute;
	justify-content: flex-start;
	width: auto;
	max-width: 85%;
}

.select2-dropdown {
	background-color: #131313;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
	background-color: #131313 !important;
	color: $color-main;
}

.select2-container--default.select2-container--focus .select2-selection--multiple {
	border-color: $color-main;
	border-radius: 0;
}

.select2-container--default .select2-results__option {
	display: flex;
	align-items: center;
	position: relative;

	&::before {
		display: block;
		content: '';
		width: 14px;
		height: 14px;
		border: 2px solid $color-main;
		margin-right: 20px;
	}
}

.select2-container--default .select2-results__option[aria-selected=true] {
	background-color: #131313 !important;

	&::after {
		content: "";
		width: 4px;
		height: 9px;
		border-right: 2px solid $color-main;
		border-bottom: 2px solid $color-main;
		position: absolute;
		top: 10px;
		left: 12px;
		transform: rotate(37deg);
	}
}

.select2-container--default .select2-selection--multiple .select2-selection__rendered li {
	position: relative;
	margin-right: 2px !important;
	padding-left: 0 !important;
	float: none;

	&::after {
		content: ',';
		display: inline-block;
	}


	&:nth-last-child(2),
	&.select2-search {
		&::after {
			display: none;
		}
	}
}

.select2-container--default .select2-results>.select2-results__options {
	padding: 9px 0 12px 28px;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
	font-size: 18px;
	color: #191919;
	background-color: transparent;
	border: none;

	span {
		display: none;
	}
}

.ymaps-2-1-79-search {
	display: none;
}

.map-block {
	position: relative;
	height: 0;
	padding-bottom: 28%;
	margin: 60px 0 52px;

	img {
		position: absolute;
		width: 100%;
		height: 100%;
		object-fit: cover;
		display: none;
	}

	&__taxi {
		position: absolute;
		left: 10%;
		bottom: 20px;
		z-index: 2;
		cursor: pointer;

		img {
			transition: transform 0.2s linear;
		}


		&:hover {

			img {
				transform: scale(0.9);
			}
		}
	}

	@media(max-width: 1024px) {
		margin: 124px 0 60px;
	}

	@media(max-width: 640px) {
		margin: 50px 0;
		padding-bottom: 65%;
	}

	@media(max-width: 575px) {
		margin: 50px 0 15px;
		padding-bottom: 113%;
	}

	&__map {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.inner-cart {
	margin: 15px 0 0;

	&__right {
		width: 100%;

		@media(max-width: 1024px) {
			margin: 40px auto 0;
		}
	}
	&__top {
		display: flex;

		@media(max-width: 1024px) {
			flex-direction: column;
		}
	}

	&__textb {
		font-size: 14px;
		color: #000000;

		span {
			font-weight: bold;
			color: $color-main;
		}
	}

	&__buy {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		text-align: center;

		.button {
			width: 100%;
		}
	}

	&__img {
		width: 100%;
		max-width: 600px;
		margin: 0 45px 0 0;

		@media(max-width: 1280px) {
			max-width: 448px;
		}

		.slider-for {
			img {
				width: 100%;
				height: 450px;
				object-fit: contain;

				@media(max-width: 640px) {
					height: 360px;
				}
			}
		}

		.slider-nav {
			img {
				width: 100%;
				object-fit: contain;
			}
		}
	}

	&__name {
		font-size: 38px;
		color: #33485d;
		font-weight: 500;
		margin: 3px 0 10px;
		max-width: 100%;

		@media(max-width: 1366px) {
			font-size: 32px;
		}

		@media(max-width: 480px) {
			font-size: 21px;
		}
	}

	&__art {
		display: flex;
		font-size: 14px;
		color: #33485d;
		font-weight: 500;
		margin: 0 0 20px;

		span {
			display: block;
			margin-right: 5px;
			font-weight: 600;
			font-size: 14px;
			color: #5c6d7d;
		}
	}

	&__price-block {
		display: flex;
	}

	&__buy-block {
		display: flex;
		align-items: center;
		margin: 0 0 42px;
	}

	&__old-price {
		display: flex;
		align-items: flex-end;
		color: #404040;
		font-size: 16px;
		font-weight: 400;
		margin: 0 12px 0 0;

		span {
			display: block;
			font-size: 20px;
			line-height: 1;
			font-weight: bold;
			margin-right: 5px;
			text-decoration: line-through;
		}
	}

	&__middle {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 0 10px;

		@media(max-width: 600px) {
		    flex-direction: column;
			align-items: flex-start;
		}
	}

	&__bonuses {
		display: flex;
		align-items: center;
		color: $color-main;
		margin: 17px 0 0;

		span {
			display: flex;
			margin-left: 8px;

			span {
				font-weight: bold;
			}
		}
	}

	&__form {
		display: flex;
		justify-content: space-between;
		margin: 0 0 34px;
	}

	&__select {
		display: flex;
		align-items: center;

		@media(max-width: 640px) {
		    flex-direction: column;
		}

		span {
			display: block;
			margin-right: 10px;
			color: #000;

			@media(max-width: 640px) {
				margin-right: 0;
				margin-bottom: 8px;
			}
		}

		select {
			width: 90px;
			height: 44px;
		}
	}

	&__text {
		color: #000;
	}

	&__size-pic {

		img {
			width: 100%;
			height: auto;
		}
	}

	&__count {
		display: flex;
		align-items: center;

		@media(max-width: 640px) {
		    flex-direction: column;
		}

		span {
			display: block;
			margin-right: 10px;
			color: #000;

			@media(max-width: 640px) {
				margin-right: 0;
				margin-bottom: 8px;
			}
		}
	}

	&__size {
		display: flex;
		color: #000000;
		margin: 42px 0 34px;

		&-body {
			font-size: 14px;
			margin-left: 10px;

			span {
				display: block;
				font-weight: bold;
				font-size: 16px;
			}
		}
	}

	&__price {
		display: flex;
		align-items: flex-end;
		color: red;
		font-size: 24px;
		font-weight: 400;
		margin: 0px 0 -4px;

		span {
			display: block;
			font-size: 42px;
			line-height: 1;
			font-weight: bold;
			margin-right: 5px;
		}
	}

	&__text {
		line-height: 1.6;

		@media(max-width: 768px) {
			font-size: 14px;
		}
	}

	&__description {
		display: flex;
		justify-content: space-between;

		@media(max-width: 1024px) {
			flex-direction: column;
		}

		.button--more {
			margin: 60px 0 0 auto;
		}
	}

	&__bottom {
		margin: 30px 0 0;
	}
}

.slider-nav {

	.slick-slide {
		border: 2px solid transparent;
		border-radius: 20px;
		height: 108px;
		cursor: pointer;

		@media(max-width: 480px) {
			height: 85px;
		}

		> div {
			display: flex;
			justify-content: center;
			align-items: center;
			height: 100%;
		}

		&.slick-current {
			border-color: $color-main;
		}

		img {
			width: 80%!important;
			height: 80%!important;
		}
	}
}

.tab-inner {

	&__top {
		display: flex;
		width: 100%;
		border-bottom: 2px solid #DDE8F0;
		margin-bottom: 23px;

		@media(max-width: 480px) {
		    flex-wrap: wrap;
			border: none;
			margin-bottom: 0;
		}
	}

	&__button {
		color: #b6b6b6;
		font-size: 18px;
		font-weight: 400;
		padding: 0 35px 16px;
		cursor: pointer;
		position: relative;

		@media(max-width: 640px) {
			font-size: 12px;
			padding: 0 10px 16px;
		}

		@media(max-width: 480px) {
			width: 100%;
			text-align: center;
			margin: 0 0 15px;
		}

		&::after {
			content: '';
			position: absolute;
			z-index: 1;
			bottom: -1px;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: $color-main;
			opacity: 0;
		}

		&.active {
			color: #404040;
			font-weight: 700;

			&::after {
				opacity: 1;
			}
		}

		&:hover {
			&::after {
				opacity: 1;
			}
		}
	}

	&__block {
		&:not(.active) {
			display: none;
		}

	}
}

.slick-arrow {
	z-index: 5;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 42px;
	height: 42px;
	background-color: $color-main;
	border-radius: 50px;
	font-size: 0;
	color: #fff;
	border: none;
	cursor: pointer;
	text-indent: -9999px;

	@media(max-width: 480px) {
		width: 37px;
		height: 37px;
	}

	&.disabled {
		background-color: #6F6F74;
		pointer-events: none;
	}

	&:first-child {
		margin-right: 13px;
	}

	&:hover {
		background-color: #f00;
	}

	&:before {
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		font-size: 20px;
		color: #fff;
		text-indent: 0;
		top: 46%;
		height: 11px;
		left: 53%;
		transform: translate(-50%,-50%);
	}
}

.slick-next {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 0;

	&::before {
		content: '›';
	}
}

.slick-prev {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 0;

	&:before {
		content: '‹';
	}
}


.star-choose {
	$this: &;

	display: flex;
	align-items: center;
	flex-wrap: wrap;
	vertical-align: top;
	user-select: none;
	font-size: 0;
	position: relative;
	margin: 0 0 40px;

	&__body {
		display: flex;
		flex-direction: row-reverse;
		justify-content: flex-end;
	}

	&__text {
		font-size: 16px;
		color: #2F3541;
		width: 100%;
		margin: 0 0 10px;
	}

	&__input {
		width: 19px;
		height: 19px;
		opacity: 0;
		position: relative;
		z-index: 10;
		float: right;
		cursor: pointer;
		padding-left: 5px;

		&.has-error {
			~ label {
				&.has-error {
					visibility: visible;
				}
			}
		}
	}

	&__svg-holder {
		float: right;
		width: 27px;
		height: 27px;
		position: relative;
		margin-right: -24px;

		svg {
			width: 27px;
			height: 27px;
			fill: #D7D7D7;
		}

		&.active {

			svg {
				fill: $color-main;
			}
		}

		#{$this}__input:hover ~ & #{$this}__svg {
			fill: $color-main;
		}

		#{$this}:not(:hover) #{$this}__input:checked ~ & #{$this}__svg { // sass-lint:disable-line force-element-nesting
			fill: $color-main;
		}

		//@for $i from 1 to 10 {
		//	&:nth-of-type(#{$i}) #{$this}__svg { // sass-lint:disable-line force-element-nesting
		//		transition-delay: #{$i * 0.03}s;
		//	}
		//}
	}

	&__svg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		max-width: 100%;
		max-height: 100%;
		margin: auto;
		fill: #ffe6ab;
		transition: fill 0.2s ease;
	}

	@for $i from 1 through 5 {
		&[data-rate="#{$i}"] {
			.star-choose__svg-holder {
				&:nth-last-of-type(-n+#{$i}) {
					.star-choose__svg {
						fill: #08C42A;
					}
				}
			}
		}
	}
}

.bottom-text {
	font-size: 21px;
	text-align: center;
	color: #333333;
	margin: 30px 0 0;

	@media(max-width: 1024px) {
	    font-size: 18px;
	}

	@media(max-width: 480px) {
		font-size: 14px;
	}

	span {
		color: #ff0000;
	}
}

.pay-bl {

	&__card {
		display: flex;
		align-items: center;
	}

	&__card-img {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100px;
		height: 60px;
		border: 1px solid #ccc;
		border-radius: 4px;

		img {
			width: 60px;
			height: 30px;
		}


		@media(max-width: 1024px) {
			width: 80px;
			height: 40px;
		}
	}

	span {
		display: block;
		margin: 0 20px;
		font-size: 24px;
		color: #333333;
		letter-spacing: 0.02em;

		@media(max-width: 1024px) {
			font-size: 20px;
		}
	}

	&__card-del {
		font-size: 20px;
		color: #ff0000;
		cursor: pointer;

		&:hover {
			color: #333333;
		}
	}

	&__add {
		display: inline-flex;
		align-items: center;
		color: #333;
		border: 1px solid $color-main;
		padding: 15px;
		margin: 20px 0 0;
		cursor: pointer;

		span {
			font-size: 20px;

			@media(max-width: 1024px) {
				font-size: 16px;
			}
		}

		&:hover {
			background-color: rgba($color-main, 0.2);
		}

		@media(max-width: 1024px) {
		    img {
			    width: 40px;
			    height: 40px;
		    }
		}
	}
}

.profile-block {
	display: flex;
	justify-content: space-between;

	@media(max-width: 1024px) {
	    flex-direction: column-reverse;
	}

	&__top-form {
		margin: 0 0 40px;
	}

	&__abon {
		border: 1px solid $color-main;
		padding: 15px;
		color: #333;
		font-size: 18px;
		margin-bottom: 10px;

		span {
			display: block;
			font-size: 14px;
			margin: 10px 0 0;
			color: $color-main;
		}
	}

	&__title {
		font-size: 24px;
		color: #333333;
		margin: 0 0 20px;
	}

	&__left {
		width: 60%;

		@media(max-width: 1024px) {
			width: 100%;
		}
	}

	&__right {
		width: 35%;
		position: sticky;
		top: 0;

		@media(max-width: 1024px) {
			width: 100%;
			margin-bottom: 20px;
			position: relative;
		}
	}

	&__item {
		display: flex;
		align-items: flex-end;
		width: 100%;
		margin-bottom: 15px;

		@media(max-width: 1024px) {
			display: block;
		}
	}

	&__save {
		height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: transparent;
		border: 1px solid $color-main;
		font-size: 18px;
		color: #333333;
		cursor: pointer;
		padding: 0 20px;
		text-align: center;

		&:hover {
			background-color: $color-main;
			color: #ffffff;
		}
	}

	&__input {
		width: 100%;
		max-width: 500px;

		label {
			display: block;
			font-size: 16px;
			color: $color-main;
			margin: 0 0 10px;
		}

		input {
			width: 100%;
			height: 50px;
			border: 1px solid #333;
			font-size: 16px;
			padding-left: 10px;

			@media(max-width: 1024px) {
				height: 40px;
			}

			&:focus {
				border-color: $color-main;
				outline: none;
			}
		}
	}

	&__change {
		font-size: 14px;
		text-transform: uppercase;
		border-bottom: 1px solid $color-main;
		padding-bottom: 5px;
		color: #333333;
		margin: 0 0 5px 15px;
		cursor: pointer;
		flex-shrink: 0;
		width: 150px;
		text-align: center;

		&:hover {
			border-color: transparent;
		}

		@media(max-width: 1024px) {
			margin: 20px 0 0;
		}
	}
}

.container {
	width: 100%;
	max-width: 1190px;
	padding: 0 15px;
	margin: 0 auto;

	&--black {

	}
}

.black-section {
	background-color: #000;
}

.help-wrap {

	&__top {
		color: #333333;
		font-size: 20px;
		margin: 0 0 20px;
		border: 1px solid $color-main;
		padding: 24px;

		@media(max-width: 1024px) {
			font-size: 16px;
		}

		a {
			display: inline-flex;
			color: $color-main;
			text-decoration: underline;

			&:hover {
				color: #333;
				text-decoration: none;
			}
		}

		&--bottom {
			margin: 20px 0 0 ;
		}
	}

	&__number {
		display: flex;
		align-items: center;
		font-family: $font-second;
		font-weight: 500;
		color: #333;
		margin-right: 15px;

		@media(max-width: 1150px) {
			font-size: 14px;
		}

		&::before {
			content: '';
			width: 5px;
			height: 5px;
			border-radius: 100%;
			flex-shrink: 0;
			display: block;
			margin-right: 12px;
			background-color: $color-main;


			@media(max-width: 480px) {
			    display: none;
			}
		}

		&:hover {
			color: $color-main;
		}
	}

	&__text {
		display: inline-flex;
		color: #333333;
		font-size: 21px;
		margin-bottom: 20px;
		border-bottom: 1px solid $color-main;
		padding: 0 0 5px;

		@media(max-width: 1024px) {
			font-size: 18px;
		}

		@media(max-width: 480px) {
			font-size: 14px;
		}

	}

	&__connect {
		display: flex;
		align-items: center;
		margin-right: 15px;

		@media(max-width: 992px) {
