		}
	}

	&__soc {
		display: block;
		margin-right: 7px;
		transition: transform 0.2s linear;

		&:hover {
			transform: scale(0.85);
		}
	}
}

.header {
	position: fixed;
	padding-top: 12px;
	z-index: 10;
	width: 100%;
	transition: top 0.3s linear;

	&__body {
		display: flex;
	}


	.logo {
		display: flex;
		align-items: center;
		flex-shrink: 0;
		margin-right: 52px;

		img {
			&:last-child {
				display: none;
			}
		}

		@media(max-width: 480px) {
			width: 90px;

			img {
				width: 100%;
				height: auto;
			}
		}
	}

	&__top {
		display: flex;
		align-items: center;

		.link {

			@media(max-width: 992px) {
				display: none;
			}
		}

		.burger {
			display: none;
		}
	}

	&__menu {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-right: 7%;

		@media(max-width: 1150px) {
			margin-right: 20%;
		}

		> .link--menu {
			margin: 0 0 -2px;

			@media(max-width: 1150px) {
				&:nth-last-child(1),
				&:nth-last-child(2) {
					display: none;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		border-bottom: 2px solid #505050;
		margin-top: 30px;
	}

	&__content {
		width: 100%;
		padding: 8px 0 0;
	}

	&__connect {
		display: flex;
		align-items: center;
		margin-right: 15px;

		@media(max-width: 992px) {
			margin-left: auto;
		}

		@media(max-width: 767px) {
		    display: none;
		}
	}

	&__soc {
		display: block;
		margin-right: 7px;
		transition: transform 0.2s linear;

		&:hover {
			transform: scale(0.85);
		}
	}

	&__soc--mob-fix {
		display: none;
	}

	&__full {
		display: flex;
		align-items: center;
		cursor: pointer;
		flex-shrink: 0;
		margin: -20px 0 0;

		> span {
			display: block;
			margin-right: 30px;
			color: rgba(255, 255, 255, .2);
			transition: color 0.15s linear;

			@media(max-width: 1150px) {
				display: none;
			}
		}

		&:hover {
			> span {
				color: $color-white;
			}

			.burger {

				span {
					&::after,
					&::before {
						width: 100%;
					}
				}
			}
		}
	}

	&__text {
		margin-right: 52px;

		@media(max-width: 1150px) {
			display: none;
		}
	}

	&__number {
		display: flex;
		align-items: center;
		font-family: $font-second;
		font-weight: 500;
		color: $color-white;
		margin-right: 15px;

		@media(max-width: 960px) {
			font-size: 14px;
		}

		&::before {
			content: '';
			width: 5px;
			height: 5px;
			border-radius: 100%;
			flex-shrink: 0;
			display: block;
			margin-right: 12px;
			background-color: $color-main;
		}

		&:hover {
			color: $color-main;
		}
	}

	&__log {
		display: flex;
		align-items: center;
		font-weight: bold;
		cursor: pointer;
		transition: color 0.15s linear;
		margin-left: 30px;
		color: $color-white;

		&:hover {
			color: $color-main;
		}

		span {
			display: block;
			margin-left: 6px;
			font-weight: 500;
		}

		&-count {
			min-width: 20px;
			height: 20px;
			border-radius: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			color: #0b0b0a;
			font-size: 13px;
			background-color: $color-main;
			padding: 4px 3px;
			line-height: 1;
		}

		&--basket {
			@media(max-width: 992px) {
				margin-right: 20px;
			}
		}
	}

	@media(max-width: 960px) {
		height: 180px;


		.link--line {
			display: none;
		}

		.header {

			&__bottom {
				display: none;
			}

			&__content {
				padding: 0;
			}

			&__text {
				display: none;
			}

			&__connect {
				display: none;

				&--mobile {
					display: flex;
					flex-wrap: wrap;
					justify-content: center;
					margin: 8px 0 0;

					.header__number {
						margin: 0 0 10px;
						width: 100%;
						font-size: 10px;
						justify-content: center;
					}

					.header__soc {
						margin-right: 15px;
					}
				}
			}

			&__top {
				align-items: flex-start;
				justify-content: space-between;

				.header__log {

					&--login {
						order: 0;
						margin: 0;

						span {
							display: none;
						}
					}

					&--basket {
						order: 1;
						margin: 0 0 0 10px;

						span {

							&:last-child {
								display: none;
							}
						}
					}
				}
			}

		}

		.burger {
			display: flex;
			order: 3;
			margin-left: 0!important;
		}

		.logo {
			flex-shrink: 0;
			margin-right: 30px;
			display: none;
		}
	}

	&.mini {
		top: -10%;

		@media(min-width: 960px) {
			background-color: #000;
			height: 75px;
			padding-top: 23px;

			.header {
				&__bottom {
					display: none;
				}

				&__content {
					padding: 0;
				}

				&__text {
					display: none;
				}

				&__top {
					.header__log {
						&:nth-child(3) {
							margin-left: 8%;
						}
					}
				}
			}

			.burger {
				display: flex;
			}

			.logo {
				flex-shrink: 0;
				margin-right: 64px;

				img {

					&:first-child {
						display: none;
					}

					&:last-child {
						display: block;
					}
				}
			}

			.header__log {

				&--login {
					img {
						display: none;
					}
				}
			}
		}

		@media(max-width: 960px) {
			background-color: #000;
			height: 50px;

			.center-mobile {
				display: none;
			}

			.header__content {
				.header__connect {
					display: flex;
					order: 1;
					margin: 0;

					.header__number {
						font-size: 9px;

						&::before {
							display: none;
						}
					}
				}
			}

		}
	}


	&.show {
		top: 0;
	}
}

.center-mobile {
	display: none;
	order: 2;
	margin: auto;
	transform: translateX(-11px);

	@media(max-width: 960px) {
	    display: block;
	}
}

.logo-mobile {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 12px;
	color: #ffffff;
	width: 200px;
}

.subscribe {
	width: 100%;
	height: 200px;
	position: relative;
	padding: 0 18px;
	cursor: pointer;

	img {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	&__body {
		position: relative;
		z-index: 2;
	}

	&__text {
		font-size: 13px;
		padding: 11px 0 4px 0;
