}

.link {

	&--line {
		color: $color-main;
		padding: 0 0 4px;
		border-bottom: 3px solid $color-main;
		transition: border-color 0.15s linear;
		cursor: pointer;

		&:hover {
			border-color: transparent;
		}
	}

	&--small {
		color: $color-main;
		font-size: 14px;
		display: flex;
		align-items: center;

		span {
			display: block;
			font-weight: 600;
			border-bottom: 2px solid $color-main;
		}

		&:hover {

			span {
				border-color: transparent;
			}
		}

		.link__arrow {
			width: 12px;

			&::after,
			&::before {
				width: 8px;
			}
		}
	}

	&--menu {
		color: $color-white;
		font-weight: 500;
		padding-bottom: 18px;
		transition: border-color 0.15s linear;
		border-color: transparent;

		&:hover,
		&.is-current {
			border-bottom: 3px solid $color-main;
		}
	}

	&--footer {
		display: block;
		font-size: 14px;
		color: #000;
		margin: 11px 0 0;
		line-height: 1.2;

		&:hover {
			color: $color-main;
			text-decoration: underline;
			font-weight: bold;
		}

		&.active {
			color: $color-main;
			text-decoration: underline;
			font-weight: bold;
		}

		@media(max-width: 1200px) {
			font-size: 12px;
		}

		@media(max-width: 768px) {
			font-size: 14px;
			margin: 11px 0 0;
		}

		@media(max-width: 575px) {
			font-size: 12px;
		}
	}

	&--dropdown {
		position: relative;

		&::after {
			content: '';
			display: block;
			position: absolute;
			right: -13px;
			top: 10px;
			width: 5px;
			height: 4px;
			margin-left: 10px;
			background-image: url(../img/m-arr.png);
			background-repeat: no-repeat;
		}
	}

	&--watch {
		color: $color-main;
		font-size: 20px;
		display: flex;
		align-items: center;

		span {
			display: block;
			font-weight: 600;
			border-bottom: 2px solid transparent;
		}

		@media(max-width: 768px) {
		    font-size: 16px;
		}

		@media(max-width: 480px) {
			font-size: 12px;
		}

		&:hover {

			span {
				border-color: $color-main;
			}
		}
	}

	&--md-show {
		display: none;

		@media(max-width: 1024px) {
			display: flex;
		}
	}

	&--md-hide {

		@media(max-width: 1024px) {
			display: none;
		}
	}

	&__arrow {
		width: 15px;
		height: 1px;
		background-color: $color-main;
		position: relative;
		margin-left: 5px;

		@media(max-width: 1024px) {
			width: 11px;
		}

		&::after,
		&::before {
			content: '';
			width: 10px;
			height: 2px;
