.slider-block {
	position: relative;
	margin-bottom: 65px;

	.block-anchor {
		top: -50px;
	}

	&.js-slider-block {
		display: none;

		&.active {
			display: block;
		}
	}

	&--prod {
		height: 534px;
		margin-bottom: 0;

		@media(max-width: 1200px) {
		    height: auto;
		}
	}

	.title {
		margin-bottom: 30px;

		&[href] {
			display: block;

			&:hover {
				color: $color-white;
			}
		}
	}

	&__item {
		position: relative;

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0,0,0, 0.5);
			transition: filter 0.3s linear, opacity 0.15s linear;
		}

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			z-index: 2;
			width: calc(100% - 4px);
			border: 2px solid #c4a562;
			height: calc(100% - 4px);
			opacity: 0;
		}

		span {
			display: block;
			font-family: $font-second;
			font-weight: 500;
			max-width: 160px;
			color: $color-white;
			position: absolute;
			bottom: 30px;
			left: 24px;
			z-index: 1;

			.recomend-block & {
				left: 10px;
				font-size: 12px;
				padding: 4px;
				background-color: rgba(0, 0, 0, 0.5);
				color: $color-white !important;
				bottom: 20px;
			}

			@media(max-width: 640px) {
				left: 10px;
				font-size: 11px;
			}
		}

		img {
			filter: grayscale(100%);
			width: 100%;
			height: auto;

			.recomend-block & {
				filter: grayscale(0);
			}
		}

		&:hover {

			> img {
				background-color: #c4a562;
				filter: grayscale(0);
			}

			span {
				color: $color-main;
			}

			&::after {
				opacity: 0;
			}

			&::before {
				opacity: 1;
			}
		}

		&--pic {
			display: block;
			position: relative;
			height: 0;
			padding-bottom: 100%;

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				filter: grayscale(0);
			}

			&::after {
				opacity: 0;
			}

			&::before {
				opacity: 0 !important;
			}

			> span {
				display: block;
				width: 24px;
				height: 24px;
				position: absolute;
				z-index: 3;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				img {
					width: 100%;
					height: 100%;
				}
			}
		}

		&--gal {
			display: block;
			position: relative;
			height: 0;
			padding-bottom: 100%;

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				filter: grayscale(0);
				object-fit: cover;
			}

			&::before {
				opacity: 0 !important;
			}

			.slider-block__item-zoom {
				display: block;
				width: 24px;
				height: 24px;
				position: absolute;
				z-index: 3;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				img {
					width: 100%;
					height: 100%;
					filter: brightness(0) invert(1);
				}
			}
		}
	}
	.owl-prev,.owl-next {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);

		.coach-block &{
			top: 32%;
		}

		.products-block & {
			top: 180px;

			@media(max-width: 1300px) {
				top: -130px;
			}
		}

		&.disabled {
			opacity: 0.4;
			cursor: default;
		}

		&::after {
			content: '';
			position: absolute;
			height: 40px;
			width: 100%;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}


		&:hover {

			> div {
				background-color: $color-hover;

				&::after,
				&::before {
					background-color: $color-hover;
				}
			}
		}
	}

	.nav-next,
	.nav-prev {
		width: 40px;
		height: 4px;
		background-color: $color-main;
		border-radius: 50px;

		@media(max-width: 575px) {
			width: 30px;
			height: 2px;
		}

		&::after,
		&::before {
			content: '';
			width: 15px;
			height: 4px;
			background-color: $color-main;
			position: absolute;
			right: 1px;
			transform-origin: right;
			border-radius: 50px;

			@media(max-width: 575px) {
				width: 10px;
				height: 2px;
			}
		}

		&::after {
			transform: rotate(45deg);
		}

		&::before {
			transform: rotate(-45deg);
		}
	}

	.owl-prev {
		left: -62px;

		@media(max-width: 1300px) {
			left: initial;
			right: 70px;
			top: -40px;
		}

		@media(max-width: 599px) {
			right: 45px;

			.products-block &,
			.coach-block & {
				top: 100%;
				left: 36%;
			}
		}
	}

	.owl-next {
		right: -62px;

		@media(max-width: 1300px) {
			right: 0;
			top: -40px;

			.products-block &,
			.coach-block & {
				right: 15px;
			}
		}

		@media(max-width: 599px) {
			.products-block &,
			.coach-block & {
				top: 100%;
				right: 36%;
			}
		}
	}

	.nav-next {

		&::after,
		&::before  {
			right: initial;
			left: 1px;
			transform-origin: left;
		}

		&::after {
			transform: rotate(45deg);
		}

		&::before {
			transform: rotate(-45deg);
		}
	}

	.nav-next {

	}
}

