			border-bottom: 2px solid #a2a2a2;

			&:focus {
				outline: none;
			}

			&::placeholder {
				font-family: $font-stack;
				font-size: 16px;
				color: #a2a2a2;
				transition: opacity 0.15s linear;
			}

