.filter {
	width: 100%;
	position: relative;
	padding-bottom: 40px;

	&__shower {
		display: inline-flex;
		justify-content: flex-end;
		position: sticky;
		z-index: 4;
		left: 100%;
		top: 100px;
		transform: translateX(calc(100% + 30px));
		opacity: 0;
		visibility: hidden;
		height: 27px;

		@media(max-width: 1024px) {
			display: none;
		}

		@media(max-width: 1024px) {
			height: 50px;
			top: 93%;
			width: 100%;
			max-width: 100%;
			transform: none;
		}

		&.show {
			opacity: 1;
			visibility: visible;
		}

		&::after {
			content: '';
			position: absolute;
			background: $color-main;
			left: -10px;
			top: 4px;
			width: 19px;
			height: 19px;
			transform: rotate(45deg);

			@media(max-width: 1024px) {
