.drop-wrap {
	position: relative;

	.drop-menu {
		position: absolute;
		z-index: 1;
		top: calc(100% + 14px);
		left: 0;
		width: 148px;
		background-color: #fff;
		border-radius: 10px;
		padding: 16px 0 20px 18px;
		opacity: 0;
		visibility: hidden;
		transition: 0.2s linear;
		transition-property: visibility, opacity;

		&::after {
			content: '';
			position: absolute;
			top: -14px;
			width: 100%;
			z-index: 1;
			left: 0;
			height: 14px;
		}

		&.open {
			opacity: 1;
			visibility: visible;
		}

		a {
			display: inline-flex;
			align-items: center;
			font-size: 16px;
			color: #0a0a09;
			font-weight: bold;

			span {
				border-bottom: 1px solid transparent;
				transition: border-color 0.2s linear;
			}

			&::before {
				content: '';
				width: 4px;
				height: 4px;
				border-radius: 6px;
				background-color: #83724c;
				margin: 0 9px 0 0;
			}

			&:hover {

				span {
					border-color: #0a0a09;
				}
			}
		}

		a + a {
			margin: 14px 0 0;
		}
	}

	&--tablet {
		display: none;

		@media(max-width: 1150px) {
			display: flex;
		}
	}
}

